<template>
    <div class="active-count-down" v-if="time">
        <van-count-down
            format="HH:mm:ss"
            use-slot
            :time="time"
            @change="onChangeDate"
            @finish="onFinished"
        >
            <div class="row-center">
                 <div class="xs mr5" v-if="timeData.days">
                    {{ timeData.days }}天
                </div>
                <div class="item xs" :style="{ color: color, 'background-color': bgColor }">
                    {{ timeData.hours }}
                </div>
                <div class="primary"  :style="{margin: '0 3px',  color: bgColor }">
                    :
                </div>
                <div class="item xs" :style="{ color: color, 'background-color': bgColor }">
                    {{ timeData.minutes }}
                </div>
                <div class="primary"  :style="{margin: '0 3px',  color: bgColor }">
                    :
                </div>
                <div class="item xs" :style="{ color: color, 'background-color': bgColor }">
                    {{ timeData.seconds }}
                </div>
            </div>
        </van-count-down>
    </div>
</template>

<script>
export default {
    props: {
        time: {
            type: Number,
            default: 0,
        },
        color: {
            type: String,
            default: "#fff",
        },
        bgColor: {
            type: String,
            default: "#FF2C3C",
        },
    },
    data() {
        return {
            timeData: {},
        };
    },
    methods: {
        onChangeDate(e) {
            const timeData = {};
            for (let prop in e) {
                timeData[prop] = ["hours", "minutes", "seconds"].includes(prop)
                    ? (timeData[prop] = ("0" + e[prop]).slice(-2))
                    : e[prop];
            }
            this.timeData = timeData;
        },
        onFinished() {
            this.$emit("finish");
        },
    },
};
</script>
<style lang="scss" scoped>
.active-count-down .item {
    text-align: center;
    width: 20px;
    line-height: 20px;
    border-radius: 3px;
}
</style>